.alpheios-alignment-editor-align-text {
  float: left;
  width: 50%;
}

.alpheios-alignment-editor-align__origin {
  border-right: 2px solid #ddd;
  padding-right: 20px;
}

.alpheios-alignment-editor-align__target {
  padding-left: 20px;
}

.alpheios-alignment-editor-align-text-segment {
  margin-bottom: 10px;
}

.alpheios-align-text-segment-origin-even,
.alpheios-align-text-segment-target-even {
  background: #dadada;
}