.alpheios-alignment-editor-text-block textarea {
  width: 100%;
  min-height: 300px;
}
.alpheios-alignment-editor-text-block p {
  margin-bottom: 10px;
}
.alpheios-alignment-editor-text-block .alpheios-alignment-editor-text-block__ava-lang span,
.alpheios-alignment-editor-text-block .alpheios-alignment-editor-text-block__other-lang span {
  min-width: 160px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-editor-text-block .alpheios-alignment-editor-text-block__ava-lang__select,
.alpheios-alignment-editor-text-block .alpheios-alignment-editor-text-block__other-lang-input-block {
  width: 80%;
  max-width: 300px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-editor-text-block .alpheios-alignment-editor-text-block__other-lang__description {
  font-size: 90%;
  color: #888;
}